import React from 'react';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Slider from '@material-ui/core/Slider';


const Datapoints = (classes) => {


    const handleBarMultiple = (value) => {
        if (localStorage.getItem('barMultiple') != value) {
            localStorage.setItem('barMultiple', value);
        }
    }

    const marks = [
        {
            value: 5,
            label: '2^5 bars',
        },
        {
            value: 8,
            label: '2^8 bars',
        },
        {
            value: 12,
            label: '2^12 bars',
        },
    ];

    return (
        <FormControl className={classes.formControl}
                     style={{minWidth: "180px", marginLeft: "30px", marginRight: "30px", marginBottom: "12px"}}>
            <InputLabel shrink id="demo-simple-select-placeholder-label-label">
                Amount of Datapoints
            </InputLabel>
            <Slider style={{marginTop: "24px"}}
                    getAriaValueText={handleBarMultiple}
                    aria-labelledby="discrete-slider"
                    valueLabelDisplay="auto"
                    step={1}
                    marks={marks}
                    min={5}
                    max={14}
                    defaultValue={localStorage.getItem('barMultiple') === null ? 9 : localStorage.getItem('barMultiple')}
            />
        </FormControl>
    );
};


export default Datapoints;