import React, {useState} from 'react'
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import TextField from "@material-ui/core/TextField";


const BarMargin = (classes) => {

    const [margin, setMargin] = useState(localStorage.getItem('barMargin') === null ? 0 : localStorage.getItem('barMargin'));

    const updateMargin = (e) => {
        try {
            if (e.target.value < 0) {
                setMargin(0);
                localStorage.setItem('barMargin', '0');
            } else if (e.target.value > 1000) {
                setMargin(1000);
                localStorage.setItem('barMargin', '1000');
            } else {
                setMargin(parseInt(e.target.value));
                if (e.target.value === '') {
                    localStorage.setItem('barMargin', '0');
                } else {
                    localStorage.setItem('barMargin', '' + parseInt(e.target.value));
                }
            }
        } catch (e) {
            setMargin(0);
            localStorage.setItem('barMargin', '0');
        }
    };


    return (
        <FormControl style={{minWidth: "180px", marginLeft: "30px", marginRight: "30px", marginBottom: "12px"}}
                     className={classes.formControl}>
            <TextField type="number" id="standard-basic" label="Bar Margin" value={margin} onChange={updateMargin}/>
        </FormControl>
    )
};

export default BarMargin;