import React, {useState} from 'react'
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import TextField from "@material-ui/core/TextField";


const LineWeight = (classes) => {

    const [lWeight, setLWeight] = useState(localStorage.getItem('lineWeight') === null ? 0 : localStorage.getItem('lineWeight'));

    const updateMargin = (e) => {
        try {
            if (e.target.value < 1) {
                setLWeight(1);
                localStorage.setItem('lineWeight', '1');
            } else if (e.target.value > 1000) {
                setLWeight(1000);
                localStorage.setItem('lineWeight', '1000');
            } else {
                setLWeight(parseInt(e.target.value));
                if (e.target.value === '') {
                    localStorage.setItem('lineWeight', '1');
                } else {
                    localStorage.setItem('lineWeight', '' + parseInt(e.target.value));
                }
            }
        } catch (e) {
            setLWeight(0);
            localStorage.setItem('lineWeight', '1');
        }
    };


    return (
        <FormControl style={{minWidth: "180px", marginLeft: "30px", marginRight: "30px", marginBottom: "12px"}}
                     className={classes.formControl}>
            <TextField type="number" id="standard-basic" label="Line Weight" value={lWeight} onChange={updateMargin}/>
        </FormControl>
    )
};

export default LineWeight;