import React from 'react'
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import ColorPicker from 'material-ui-color-picker'


const BarColor = (classes) => {

    const setColor = (color) => {
        if (color !== undefined && localStorage.getItem('barcolor') !== color) {
            localStorage.setItem('barcolor', color);
        }
        if (color === undefined) {
            global.refreshCanvas();
        }
    };

    return (
        <FormControl style={{minWidth: "180px", marginLeft: "30px", marginRight: "30px", marginBottom: "12px"}}
                     className={classes.formControl}>
            <InputLabel shrink id="demo-simple-select-placeholder-label-label">
                Bar Color
            </InputLabel>
            <ColorPicker style={{marginTop: "18px"}}
                         name='color'
                         defaultValue='#ffffff'
                // value={this.state.color} - for controlled component

                         onChange={setColor}

            />
        </FormControl>
    )
};

export default BarColor;