import React from 'react';
import {makeStyles} from '@material-ui/core/styles';
import Datapoints from './Datapoints';
import BarColor from './BarColor';
import BackgroundColor from './BackgroundColor';
import SoundRange from './SoundRange';
import Height from './Height';
import BarMargin from "./BarMargin";
import LineColor from "./LineColor";
import LineWeight from "./LineWeight";
import BarType from "./BarType";

const useStyles = makeStyles((theme) => ({
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
    },
    selectEmpty: {
        marginTop: theme.spacing(2),
    },
}));

const Selection = () => {
    const classes = useStyles();

    return (
        <div style={{margin: "20px"}}>
            <div className="s-container">
                <BarType classes={classes}/>
                <BarColor classes={classes}/>
                <BarMargin classes={classes}/>
            </div>
            <div className="s-container">
                <Datapoints classes={classes}/>
                <SoundRange classes={classes}/>
                <Height classes={classes}/>
                <LineColor classes={classes}/>
                <LineWeight classes={classes}/>
                <BackgroundColor classes={classes}/>
            </div>

        </div>
    );
}


export default Selection;