import {useState} from 'react';
import './App.css';
import Selection from './components/Selection';
import Button from '@material-ui/core/Button';

import {createMuiTheme, ThemeProvider} from '@material-ui/core/styles';

function App() {
    const [reload, setReload] = useState(0);

    global.refreshCanvas = () => {
        setReload(reload + 1);
    };

    const theme = createMuiTheme({
        palette: {
            type: 'dark',
        },
    });
    return (
        <div className="App">
            <ThemeProvider theme={theme}>
                <Selection/>

                <div id="full-screen">
                    <Button variant="outlined" onClick={() => setReload(reload + 1)}>reload Cavas</Button> &nbsp;
                    <Button variant="outlined" href="/visualizer" target="_blank"> Open Visualizer in
                        Fullscreen</Button>
                </div>
            </ThemeProvider>
            <iframe key={reload} src="/visualizer" title={reload}/>
        </div>
    );
}

export default App;
